import Config from "../../config";
import httpService from "../../httpService";

// Not working when old token expires and new one is created.  seems to be sending the old one
// Axios.defaults.headers.common["Authorization"] = `Bearer ${token}`;

const defaultState = {
  practice: []
};
const getters = {
  PRACTICE: state => {
    return state.practice;
  }
};
const mutations = {
  SET_PRACTICE: (state, payload) => {
    state.practice = payload;
  },
  UPDATE_PRACTICE: (state, payload) => {
    const id = state.practice.findIndex(elem => elem.id === payload.id);

    state.practice[id] = Object.assign({}, payload);
  },
  NEW_PRACTICE: (state, payload) => {
    state.practice.push(payload);
  },
  DELETE_PRACTICE: (state, payload) => {
    state.practice = state.practice.filter(elem => elem.id != payload.id);
  }
};
const actions = {
  FETCH_PRACTICE: async context => {
    httpService.get(`${Config.apiRoot}/practice`, function(status, data) {
      context.commit("SET_PRACTICE", data);
    });
  },
  UPDATE_PRACTICE: async ({ commit }, payload) => {
    return new Promise((resolve, reject) => {
      httpService.put(`${Config.apiRoot}/practice/${payload.id}`, payload, function(status, data) {
        if (!data.success) {
          reject(data.message);
        }
        commit("UPDATE_PRACTICE", payload);
        resolve(true);
      });
    });
  },
  NEW_PRACTICE: async ({ commit }, payload) => {
    return new Promise((resolve, reject) => {
      httpService.post(`${Config.apiRoot}/practice`, payload, function(status, data) {
        if (!data.success) {
          reject(data.message);
        }
        commit("NEW_PRACTICE", payload);
        resolve(true);
      });
    });
  },
  DELETE_PRACTICE: async ({ commit }, payload) => {
    return new Promise((resolve, reject) => {
      httpService.delete(`${Config.apiRoot}/practice/${payload.id}`, payload, function(status, data) {
        if (!data.success) {
          reject(data.message);
        }
        commit("DELETE_PRACTICE", payload);
        resolve(true);
      });
    });
  }
};

export default {
  state: defaultState,
  getters,
  mutations,
  actions
};
