import Config from "../../config";
import httpService from "../../httpService";

const defaultState = {
  entity: []
};
const getters = {
  ENTITY: state => {
    return state.entity;
  }
};
const mutations = {
  SET_ENTITY: (state, payload) => {
    state.entity = payload;
  },
  UPDATE_ENTITY: (state, payload) => {
    const id = state.entity.findIndex(elem => elem.id === payload.id);

    state.entity[id] = Object.assign({}, payload);
  },
  NEW_ENTITY: (state, payload) => {
    state.entity.push(payload);
  },
  DELETE_ENTITY: (state, payload) => {
    state.entity = state.entity.filter(elem => elem.id != payload.id);
  }
};
const actions = {
  FETCH_ENTITY: context => {
    httpService.get(`${Config.apiRoot}/entity`, function(status, data) {
      context.commit("SET_ENTITY", data);
    });
  },
  UPDATE_ENTITY: async ({ commit }, payload) => {
    return new Promise((resolve, reject) => {
      httpService.put(`${Config.apiRoot}/entity/${payload.id}`, payload, function(status, data) {
        if (!data.success) {
          reject(data.message);
        }
        commit("UPDATE_ENTITY", payload);
        resolve(true);
      });
    });
  },
  NEW_ENTITY: async ({ commit }, payload) => {
    return new Promise((resolve, reject) => {
      httpService.post(`${Config.apiRoot}/entity`, payload, function(status, data) {
        if (!data.success) {
          reject(data.message);
        }
        payload.id = data.new_id;
        commit("NEW_ENTITY", payload);
        resolve(true);
      });
    });
  },
  DELETE_ENTITY: async ({ commit }, payload) => {
    return new Promise((resolve, reject) => {
      httpService.delete(`${Config.apiRoot}/entity/${payload.id}`, function(status, data) {
        if (!data.success) {
          reject(data.message);
        }
        commit("DELETE_ENTITY", payload);
        resolve(true);
      });
    });
  }
};

export default {
  state: defaultState,
  getters,
  mutations,
  actions
};
