import Config from "../../config";
import httpService from "../../httpService";

const defaultState = {
  physicians: []
};
const getters = {
  PHYSICIAN: state => {
    return state.physicians;
  }
};
const mutations = {
  SET_PHYSICIAN: (state, payload) => {
    state.physicians = payload;
    //TODO Object.assign({}, payload)
  },
  UPDATE_PHYSICIAN: (state, payload) => {
    const id = state.physicians.findIndex(elem => elem.npi === payload.npi);

    state.physicians[id] = Object.assign({}, payload);
  },
  NEW_PHYSICIAN: (state, payload) => {
    state.physicians.push(payload);
  }
};
const actions = {
  FETCH_PHYSICIAN: async context => {
    httpService.get(`${Config.apiRoot}/physician`, function(status, data) {
      context.commit("SET_PHYSICIAN", data);
    });
  },
  UPDATE_PHYSICIAN: async ({ commit }, payload) => {
    return new Promise((resolve, reject) => {
      httpService.put(`${Config.apiRoot}/physician/${payload.npi}`, payload, function(status, data) {
        if (!data.success) {
          reject(data.message);
        }
        commit("UPDATE_PHYSICIAN", payload);
        resolve(true);
      });
    });
  },
  NEW_PHYSICIAN: async ({ commit }, payload) => {
    return new Promise((resolve, reject) => {
      httpService.post(`${Config.apiRoot}/physician`, payload, function(status, data) {
        if (!data.success) {
          reject(data.message);
        }
        commit("NEW_PHYSICIAN", payload);
        resolve(true);
      });
    });
  }
};

export default {
  state: defaultState,
  getters,
  mutations,
  actions
};
