import Config from "../../config";
import httpService from "../../httpService";

const defaultState = {
  lists: []
};
const getters = {
  APPLICATION_LISTS: state => {
    return state.lists;
  }
};
const mutations = {
  SET_APPLICATION_LISTS: (state, payload) => {
    state.lists = payload;
  }
};
const actions = {
  FETCH_APPLICATION_LISTS: async context => {
    httpService.get(`${Config.apiRoot}/application_lists`, function(status, data) {
      context.commit("SET_APPLICATION_LISTS", data);
    });
  }
};

export default {
  state: defaultState,
  getters,
  mutations,
  actions
};
