// https://gist.github.com/paulsturgess/ebfae1d1ac1779f18487d3dee80d1258
import axios from "axios";
import authentication from "./authentication";

class Service {
  constructor() {
    let service = axios.create({});
    service.interceptors.response.use(this.handleSuccess, this.handleError);
    this.service = service;
  }
  //  headers: { Authorization: `Bearer ${token}` }
  handleSuccess(response) {
    return response;
  }

  handleError = error => {
    // switch (error.response.status) {
    //   case 401:
    //     this.redirectTo(document, "/");
    //     break;
    //   case 404:
    //     this.redirectTo(document, "/404");
    //     break;
    //   default:
    //     this.redirectTo(document, "/500");
    //     break;
    // }
    return Promise.reject(error);
  };

  redirectTo = (document, path) => {
    document.location = path;
  };

  get(path, callback) {
    authentication.acquireToken().then(token => {
      //      return this.service.get(path).then(response => callback(response.status, response.data));
      return this.service
        .request({
          method: "GET",
          url: path,
          responseType: "json",
          headers: { Authorization: `Bearer ${token}` }
        })
        .then(response => callback(response.status, response.data));
    });
  }

  put(path, payload, callback) {
    authentication.acquireToken().then(token => {
      return this.service
        .request({
          method: "PUT",
          url: path,
          responseType: "json",
          data: payload,
          headers: { Authorization: `Bearer ${token}` }
        })
        .then(response => callback(response.status, response.data));
    });
  }

  post(path, payload, callback) {
    authentication.acquireToken().then(token => {
      return this.service
        .request({
          method: "POST",
          url: path,
          responseType: "json",
          data: payload,
          headers: { Authorization: `Bearer ${token}` }
        })
        .then(response => callback(response.status, response.data));
    });
  }

  delete(path, callback) {
    authentication.acquireToken().then(token => {
      return this.service
        .request({
          method: "DELETE",
          url: path,
          responseType: "json",
          headers: { Authorization: `Bearer ${token}` }
        })
        .then(response => callback(response.status, response.data));
    });
  }
}

export default new Service();
